.contacts {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.contacts__title {
  margin-bottom: 50px;
  color: #ffffff;

  &::after {
    background-color: #ffffff;
  }
}

.contacts__info {
  min-width: 300px;
  width: 23vw;
  font-size: 14px;

  a {
    color: #ffffff;
  }
}

.contacts__info-address {
  margin-bottom: 20px;
  font-style: normal;


  span {
    font-weight: 700;
    font-size: 18px;
    text-transform: uppercase;
  }
}

.contacts__info-tel {
  display: block;
  margin-bottom: 20px;
  font-weight: 700;
  font-size: 18px;
  text-transform: uppercase;

  span {
    font-weight: 400;
    font-size: 16px;
    transition: 0.2s ease-in-out;

    &:hover,
    &:focus {
      color: @color_text_hover;
    }

    &:active {
      opacity: 0.6;
    }
  }
}

.contacts__info-email {
  display: inline-block;
  margin-bottom: 20px;
  transition: 0.2s ease-in-out;

  &:hover,
  &:focus {
    color: @color_text_hover;
  }

  &:active {
    opacity: 0.6;
  }
}

.contacts__info-timerable {
  margin: 0;
  margin-bottom: 20px;
}

.contacts__form {
  display: flex;
  flex-direction: column;
  min-width: 300px;
  width: 40vw;
  max-width: 600px;
}

.contacts__form-item {
  margin: 0;
  margin-bottom: 30px;
}

.contacts__form-input,
.contacts__form-textarea {
  width: 100%;
  padding: 20px;
  box-sizing: border-box;
  color: #ffffff;
  border: 1px solid #ffffff;
  background-color: transparent;
  transition: border-color 0.2s ease-in-out;

  &:hover,
  &:focus {
    border-color: @color_text_hover;
    outline: none;
  }

  &::placeholder {
    color: #ffffff;
    transition: 0.2s ease-in-out;
  }

  &:hover::placeholder {
    opacity: 0.7;
  }
}

.contacts__form-textarea {
  height: 160px;
  resize: vertical;
}
