.workers {
  position: relative;
  margin-bottom: 60px;
  padding-top: 20px;
  padding-bottom: 100px;

  &::before {
    content: "";
    position: absolute;
    bottom: 97%;
    z-index: 1;
    width: 100%;
    height: 80px;
    background-size: 130% 80px;
    background-image: url(../img/decor/decor-elem-2.svg);
  }

  &::after {
    content: "";
    position: absolute;
    bottom: 0;
    z-index: -1;
    width: 100%;
    height: 80px;
    background-size: 115% 100px;
    background-image: url(../img/decor/decor-elem-1.svg);
  }
}

.workers__container {
  display: flex;
  flex-wrap: wrap;
  max-width: 1150px;
  margin: 0 auto;
  padding: 0 50px;
}

.workers__title {
	margin-bottom: 50px;
}

.workers__wrapper {
	display: flex;
	justify-content: space-between;
}

.workers__list {
  margin: 0;
  margin-left: 50px;
	padding: 0;
	list-style: none;
}

.workers-list {
	display: flex;
	flex-wrap: wrap;
	width: 60%;
	height: 100%;
}

.workers-list__item {
  min-width: 180px;
  width: 30%;

  &:nth-child(3n+2) {
    margin: 0 30px;
  }
}

.workers-list__img {
	display: block;
  width: 100%;
  height: 250px;
	margin-bottom: 30px;
}

.workers-list__name {
	margin: 0;
	margin-bottom: 10px;
	font-size: 18px;
	text-transform: uppercase;
}

.workers-list__position {
	margin: 0;
  margin-bottom: 40px;
  font-size: 0.8em;
}

.workers__info {
  align-self: flex-start;
  min-width: 270px;
  max-width: 30%;
	padding: 40px;
	border: 2px solid #03587f;
}

.workers-info__quantity {
	position: relative;
	display: inline-block;
	margin-bottom: 10px;
  font-size: 30px;

  &::after {
    content: "";
    position: absolute;
    left: calc(100% + 20px);
    bottom: 6px;
    width: calc(100% + 10px);
    height: 2px;
    background-color: #03587f;
  }
}

.workers-info__title {
	margin: 0;
	margin-bottom: 30px;
}

.workers-info__text {
	margin: 0;
}
