.about {
  position: relative;
  margin-bottom: 80px;
  width: 100%;
  height: 450px;
  margin-bottom: 100px;
  background-color: #000000;

  &::after {
    content: "";
    position: absolute;
    top: 100%;
    z-index: -1;
    width: 100%;
    height: 150px;
    background-size: 195% 150px;
    background-image: url(../img/decor/decor-elem-2.svg);
  }
}

.about__video {
  position: absolute;
  z-index: 1;
  width: 100%;
  height: 100%;
  overflow: hidden;

  video {
    position: relative;
    top: -40%;
    min-height: 100%;
    min-width: 100%;
  }
}

.about__title {
  position: relative;
  z-index: 10;
  left: 50%;
  display: flex;
  flex-direction: column;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  background-image: url("../img/decor/texture.png");
  transform: translateX(-50%);

  h1 {
    width: 75%;
    margin: auto;
    font-size: 36px;
    line-height: 40px;
    letter-spacing: 2px;
    text-align: center;
  }

  .title {
    position: relative;
    left: 50%;
    display: block;
    transform: translateX(-50%);

    span {
      position: relative;
      display: inline;
      min-width: 10px;
      min-height: 10px;
      text-shadow: @shadow 1px 1px, @shadow 2px 2px, @shadow 3px 3px, @shadow 4px 4px;
      transform: skew(-10deg);
    }
  }

  .title {
    &:nth-child(1) {
      color: #ffffff;
    }

    &:nth-child(2) {
      color: @color_text_hover;
    }
  }
}
