.partners {
  position: relative;
  margin-bottom: 10px;
  padding-bottom: 40px;

  &::after {
    .after-decor();
  }
}

.partners__wrapper {
  max-width: 1150px;
  margin: 0 auto;
  padding: 0 50px;
}

.partners__title {
  margin-bottom: 40px;
}

.partners__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.partners-list {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.partners-list__item {
  min-width: 230px;
  width: 25%;
  margin: auto;
}

.partners-list__img {
  display: flex;
  height: 130px;
  filter: grayscale(1);
  transition: filter 0.2s ease-in-out,
    opacity 0.2s ease-in-out;

  &:hover {
    filter: grayscale(0);
  }

  &:active {
    opacity: 0.6;
  }

  img {
    display: block;
    margin: auto;
  }
}
