.social__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.social-list {
  display: flex;
  flex-wrap: wrap;
  width: 250px;
}

.social-list__item {
  display: flex;
  margin-right: 10px;

  &:last-child {
    margin-right: 0;
  }
}

.social__link {
  display: flex;
  margin: auto;
  padding: 10px;
  opacity: 1;
  transition: opacity 0.2s ease-in-out;

  svg {
    fill: #ffffff;
    transition: 0.2s ease-in-out;
  }

  &:hover,
  &:focus {
    svg {
      fill: @color_text_hover;
    }
  }

  &:active {
    opacity: 0.6;
  }
}
