.catalog {
  margin-bottom: 30px;
}

.catalog__list {
  .list-reset();
}

.catalog-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.catalog-list__item {
  display: flex;
  flex-direction: column;
  width: 45%;
  margin-bottom: 50px;
  border: 1px solid #f2f2f2;
  transition: box-shadow 0.2s ease-in-out;

  &:hover {
    box-shadow: 0 10px 14px -5px rgba(0, 0, 0, 0.6);
  }

  p {
    margin: 0;
    padding: 20px;
  }
}

.catalog-list__link {
  text-align: center;
  color: @color_text;

  img {
    display: block;
    width: 100%;
    height: 250px;
    padding: 10px;
  }
}

.catalog-list__link-title {
  margin: 0;
  padding: 20px 0;
  transition: color 0.2s ease-in-out,
    opacity 0.2s ease-in-out;

  &:hover,
  &:focus {
    color: @color_text_hover;
  }

  &:active {
    opacity: 0.6;
  }
}
