.block-news {
  margin-bottom: 80px;

  hr {
    margin: 30px 0;
    height: 1px;
    border: none;
    background-image: linear-gradient(to left, #fff, #03587f, #fff);
  }

  h3 {
    margin: 0;
    margin-bottom: 20px;
  }

  p {
    margin: 0;
    margin-bottom: 10px;
  }
}

.block-news__list {
  .list-reset();
}

.block-news__date {
  display: block;
  font-weight: normal;
  font-size: 0.8em;
  color: #cccccc;
}

.block-news__img {
  float: left;
  margin-right: 20px;
  margin-bottom: 20px;

  img {
    display: block;
    width: 350px;
    height: 250px;
  }
}

.block-news__img-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 20px;

  img {
    display: block;
    width: 200px;
    height: 200px;

    &:last-child {
      margin-right: 0;
    }
  }
}
