.site-list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  max-width: 700px;
}

.site-list__item {
  padding: 10px 20px;

  &:last-child {
    margin-right: auto;
  }
}

.site-list__link {
  display: block;
  color: #03587f;
  text-transform: uppercase;
  transition: color 0.2s ease-in-out,
    opacity 0.2s ease-in-out;

  &[href]:hover,
  &[href]:focus {
    color: @color_text_hover;
  }

  &[href]:active {
    opacity: 0.6;
  }

  &--active {
    border-bottom: 3px solid #03587f;
    opacity: 1;
    cursor: default;

    &:hover,
    &:focus,
    &:active {
      opacity: 1;
    }
  }
}
