@shadow: @color_text;

.page-inner .page-header {
  margin-bottom: 20px;
}

.page-header__wrapper {
  border-bottom: 1px solid #03587f;
  background-color: rgba(255, 255, 255, 1);
}

.page-header__logo {
  display: block;
  transition: 0.2s ease-in-out;

  &[href]:hover,
  &[href]:focus {
    opacity: 0.7;
  }

  &[href]:active {
    opacity: 0.3;
  }
}
