@font-face {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/roboto.woff2") format("woff2"), url("../fonts/roboto.woff") format("woff");
}
@font-face {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/roboto-bold.woff2") format("woff2"), url("../fonts/roboto-bold.woff") format("woff");
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 16px;
  line-height: 24px;
  font-weight: normal;
  color: #03587f;
  background-color: #ffffff;
}
img {
  max-width: 100%;
  height: auto;
}
a {
  text-decoration: none;
}
.container {
  max-width: 1150px;
  margin: 0 auto;
  padding: 0 50px;
}
.btn {
  display: inline-block;
  padding: 10px 50px;
  font-size: 15px;
  line-height: 24px;
  color: #03587f;
  text-transform: uppercase;
  vertical-align: middle;
  border-radius: 29px;
  border: 2px solid #03587f;
  background-color: transparent;
  transition: color 0.2s ease-in-out, border-color 0.2s ease-in-out, box-shadow 0.2s ease-in-out;
  cursor: pointer;
}
.btn:hover,
.btn:focus {
  color: #00aeff;
  border-color: #00aeff;
  outline: none;
}
.btn:active {
  box-shadow: 0 2px 5px 0 rgba(0, 174, 255, 0.6);
}
.btn__about {
  margin: 0 auto;
  margin-bottom: 30px;
  color: #00aeff;
  border-color: #00aeff;
}
.btn__goods {
  align-self: flex-start;
  margin-left: auto;
}
.btn__news {
  align-self: flex-start;
  margin-left: auto;
}
.btn__workers {
  align-self: flex-start;
  margin-left: auto;
}
.btn__form {
  align-self: center;
  color: #ffffff;
  border-color: #ffffff;
}
.page-title {
  margin: 0;
  margin-bottom: 30px;
  padding: 76px 0;
  font-size: 27px;
  line-height: 30px;
  font-weight: 700;
  color: #000000;
  text-align: center;
}
.goods__title,
.news__title,
.workers__title,
.partners__title,
.contacts__title {
  position: relative;
  display: inline-block;
  margin: 0;
  font-weight: 700;
  font-size: 36px;
  line-height: 1;
  color: #03587f;
  text-transform: uppercase;
}
.goods__title::after,
.news__title::after,
.workers__title::after,
.partners__title::after,
.contacts__title::after {
  content: "";
  position: absolute;
  left: calc(100% + 20px);
  bottom: 6px;
  width: 60px;
  height: 2px;
  background-color: #03587f;
}
.page-inner .page-header {
  margin-bottom: 20px;
}
.page-header__wrapper {
  border-bottom: 1px solid #03587f;
  background-color: #ffffff;
}
.page-header__logo {
  display: block;
  transition: 0.2s ease-in-out;
}
.page-header__logo[href]:hover,
.page-header__logo[href]:focus {
  opacity: 0.7;
}
.page-header__logo[href]:active {
  opacity: 0.3;
}
.main-nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 100px;
  padding: 0 50px;
}
.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.site-list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  max-width: 700px;
}
.site-list__item {
  padding: 10px 20px;
}
.site-list__item:last-child {
  margin-right: auto;
}
.site-list__link {
  display: block;
  color: #03587f;
  text-transform: uppercase;
  transition: color 0.2s ease-in-out, opacity 0.2s ease-in-out;
}
.site-list__link[href]:hover,
.site-list__link[href]:focus {
  color: #00aeff;
}
.site-list__link[href]:active {
  opacity: 0.6;
}
.site-list__link--active {
  border-bottom: 3px solid #03587f;
  opacity: 1;
  cursor: default;
}
.site-list__link--active:hover,
.site-list__link--active:focus,
.site-list__link--active:active {
  opacity: 1;
}
.about {
  position: relative;
  margin-bottom: 80px;
  width: 100%;
  height: 450px;
  margin-bottom: 100px;
  background-color: #000000;
}
.about::after {
  content: "";
  position: absolute;
  top: 100%;
  z-index: -1;
  width: 100%;
  height: 150px;
  background-size: 195% 150px;
  background-image: url(../img/decor/decor-elem-2.svg);
}
.about__video {
  position: absolute;
  z-index: 1;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.about__video video {
  position: relative;
  top: -40%;
  min-height: 100%;
  min-width: 100%;
}
.about__title {
  position: relative;
  z-index: 10;
  left: 50%;
  display: flex;
  flex-direction: column;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  background-image: url("../img/decor/texture.png");
  transform: translateX(-50%);
}
.about__title h1 {
  width: 75%;
  margin: auto;
  font-size: 36px;
  line-height: 40px;
  letter-spacing: 2px;
  text-align: center;
}
.about__title .title {
  position: relative;
  left: 50%;
  display: block;
  transform: translateX(-50%);
}
.about__title .title span {
  position: relative;
  display: inline;
  min-width: 10px;
  min-height: 10px;
  text-shadow: #03587f 1px 1px, #03587f 2px 2px, #03587f 3px 3px, #03587f 4px 4px;
  transform: skew(-10deg);
}
.about__title .title:nth-child(1) {
  color: #ffffff;
}
.about__title .title:nth-child(2) {
  color: #00aeff;
}
.about-content {
  margin-bottom: 80px;
}
.about-content__img {
  display: flex;
  flex-direction: column;
  float: left;
}
.about-content__img img {
  width: 323px;
  margin-right: 15px;
}
.goods {
  position: relative;
  margin-bottom: 60px;
  padding-bottom: 40px;
}
.goods::after {
  content: "";
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 2px;
  background-image: linear-gradient(to left, #ffffff, #03587f, #ffffff);
}
.goods__wrapper {
  display: flex;
  flex-wrap: wrap;
  max-width: 1150px;
  margin: 0 auto;
  padding: 0 50px;
}
.goods__title {
  margin-bottom: 50px;
}
.goods__list {
  margin: 0;
  margin-left: 50px;
  padding: 0;
  list-style: none;
}
.goods-list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 100%;
}
.goods-list__item {
  flex-grow: 1;
  width: 31%;
  max-width: 50%;
  margin-right: 30px;
  margin-bottom: 30px;
  border-radius: 15px;
  border-bottom: 5px solid #03587f;
  box-shadow: 0 4px 5px 2px #efefef;
  transition: border-color 0.2s ease-in-out, box-shadow 0.2s ease-in-out;
}
.goods-list__item:nth-child(3n),
.goods-list__item:last-child {
  margin-right: 0;
}
.goods-list__item:hover {
  border-color: #00aeff;
  box-shadow: 0 4px 5px 2px #dadada;
}
.goods-list__link {
  display: flex;
  flex-direction: column;
  color: #03587f;
}
.goods-list__img {
  display: block;
  width: 100%;
  height: 250px;
  margin: auto;
  padding: 10px;
  border-bottom: 1px solid #efefef;
}
.goods-list__title {
  margin: 0;
  padding: 20px 0;
  text-align: center;
}
.catalog {
  margin-bottom: 30px;
}
.catalog__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.catalog-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.catalog-list__item {
  display: flex;
  flex-direction: column;
  width: 45%;
  margin-bottom: 50px;
  border: 1px solid #f2f2f2;
  transition: box-shadow 0.2s ease-in-out;
}
.catalog-list__item:hover {
  box-shadow: 0 10px 14px -5px rgba(0, 0, 0, 0.6);
}
.catalog-list__item p {
  margin: 0;
  padding: 20px;
}
.catalog-list__link {
  text-align: center;
  color: #03587f;
}
.catalog-list__link img {
  display: block;
  width: 100%;
  height: 250px;
  padding: 10px;
}
.catalog-list__link-title {
  margin: 0;
  padding: 20px 0;
  transition: color 0.2s ease-in-out, opacity 0.2s ease-in-out;
}
.catalog-list__link-title:hover,
.catalog-list__link-title:focus {
  color: #00aeff;
}
.catalog-list__link-title:active {
  opacity: 0.6;
}
.catalog-content {
  display: flex;
  margin-bottom: 80px;
}
.catalog-content hr {
  margin: 30px 0;
  height: 1px;
  border: none;
  background-image: linear-gradient(to left, #fff, #03587f, #fff);
}
.catalog-content h3 {
  margin: 0;
  margin-bottom: 20px;
}
.catalog-content p {
  margin: 0;
  margin-bottom: 10px;
}
.catalog-content__left-col {
  width: 40%;
  margin-right: 40px;
}
.catalog-content__left-col img {
  display: block;
  width: 100%;
  height: 300px;
  margin-bottom: 30px;
}
.catalog-content__left-col img:last-child {
  margin: 0;
}
.catalog-content__right-col {
  width: 50%;
}
.map {
  position: relative;
  min-height: 350px;
}
.map::before {
  content: "";
  width: 100%;
  height: 80px;
  z-index: 100;
  position: absolute;
  background-size: 200% 160px;
  background-image: url(../img/decor/decor-wave.svg);
}
.map__img {
  position: absolute;
  z-index: -1;
  width: 100%;
}
.map__img img {
  display: block;
  height: 100%;
  margin: auto;
}
.news {
  position: relative;
  margin-bottom: 60px;
  padding-bottom: 40px;
}
.news__wrapper {
  display: flex;
  flex-wrap: wrap;
  max-width: 1150px;
  margin: 0 auto;
  padding: 0 50px;
}
.news__title {
  margin-bottom: 50px;
}
.news__list {
  margin: 0;
  margin-left: 50px;
  padding: 0;
  list-style: none;
}
.news-list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 100%;
}
.news-list__item {
  position: relative;
  min-width: 250px;
  width: 31%;
  margin-right: 30px;
  margin-bottom: 30px;
  border-radius: 15px;
  border-bottom: 5px solid #03587f;
  box-shadow: 0 4px 5px 2px #efefef;
  transition: border-color 0.2s ease-in-out, box-shadow 0.2s ease-in-out;
}
.news-list__item:nth-child(3n),
.news-list__item:last-child {
  margin-right: 0;
}
.news-list__item:hover {
  border-color: #00aeff;
  box-shadow: 0 5px 5px 2px #dadada;
}
.news-list__item:hover .news-item__details-link {
  background-color: rgba(0, 135, 197, 0.8);
  opacity: 1;
  transition: 0.2s ease-in-out;
}
.news-item__img-wrapper {
  position: relative;
}
.news-item__img {
  display: block;
  width: 100%;
  height: 156px;
  margin: auto;
  padding: 10px;
  padding-bottom: 0;
}
.news-item__details-link {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  text-align: center;
  border-radius: 15px 15px 0 0;
  background-color: #0087c5;
  opacity: 0;
}
.news-item__details-link {
  display: block;
  padding: 66px 0;
  color: #fff;
}
.news-item__details-link:hover {
  text-decoration: underline;
}
.news-item__desc {
  padding: 20px;
}
.news-item__title {
  margin: 0;
  margin-bottom: 20px;
}
.news-item__text {
  height: 100px;
  margin: 0;
  color: #002744;
  overflow: hidden;
}
.news-item__date {
  color: #777777;
}
.news-content {
  margin-bottom: 40px;
}
.news-content__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  list-style: none;
}
.news-content__item {
  width: 32%;
  margin-bottom: 50px;
  border: 1px solid #f2f2f2;
  transition: box-shadow 0.2s ease-in-out;
}
.news-content__item:hover {
  box-shadow: 0 10px 14px -5px rgba(0, 0, 0, 0.6);
}
.news-content__item img {
  display: block;
  width: 100%;
  height: 250px;
  padding-top: 10px;
}
.news-content__item-desc {
  display: flex;
  flex-wrap: wrap;
  padding: 10px;
}
.news-content__item-title {
  margin: 0;
  margin-bottom: 10px;
  font-weight: 700;
  color: #03587f;
  text-align: center;
}
.news-content__item-text {
  height: 100px;
  margin: 0;
  color: #002744;
  overflow: hidden;
}
.news-content__item-date {
  color: #777777;
}
.news-content__item-more {
  margin-left: auto;
  color: #03587f;
  transition: color 0.2s ease-in-out, opacity 0.2s ease-in-out;
}
.news-content__item-more:hover,
.news-content__item-more:focus {
  color: #00aeff;
}
.news-content__item-more:active {
  opacity: 0.6;
}
.block-news {
  margin-bottom: 80px;
}
.block-news hr {
  margin: 30px 0;
  height: 1px;
  border: none;
  background-image: linear-gradient(to left, #fff, #03587f, #fff);
}
.block-news h3 {
  margin: 0;
  margin-bottom: 20px;
}
.block-news p {
  margin: 0;
  margin-bottom: 10px;
}
.block-news__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.block-news__date {
  display: block;
  font-weight: normal;
  font-size: 0.8em;
  color: #cccccc;
}
.block-news__img {
  float: left;
  margin-right: 20px;
  margin-bottom: 20px;
}
.block-news__img img {
  display: block;
  width: 350px;
  height: 250px;
}
.block-news__img-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 20px;
}
.block-news__img-wrapper img {
  display: block;
  width: 200px;
  height: 200px;
}
.block-news__img-wrapper img:last-child {
  margin-right: 0;
}
.workers {
  position: relative;
  margin-bottom: 60px;
  padding-top: 20px;
  padding-bottom: 100px;
}
.workers::before {
  content: "";
  position: absolute;
  bottom: 97%;
  z-index: 1;
  width: 100%;
  height: 80px;
  background-size: 130% 80px;
  background-image: url(../img/decor/decor-elem-2.svg);
}
.workers::after {
  content: "";
  position: absolute;
  bottom: 0;
  z-index: -1;
  width: 100%;
  height: 80px;
  background-size: 115% 100px;
  background-image: url(../img/decor/decor-elem-1.svg);
}
.workers__container {
  display: flex;
  flex-wrap: wrap;
  max-width: 1150px;
  margin: 0 auto;
  padding: 0 50px;
}
.workers__title {
  margin-bottom: 50px;
}
.workers__wrapper {
  display: flex;
  justify-content: space-between;
}
.workers__list {
  margin: 0;
  margin-left: 50px;
  padding: 0;
  list-style: none;
}
.workers-list {
  display: flex;
  flex-wrap: wrap;
  width: 60%;
  height: 100%;
}
.workers-list__item {
  min-width: 180px;
  width: 30%;
}
.workers-list__item:nth-child(3n+2) {
  margin: 0 30px;
}
.workers-list__img {
  display: block;
  width: 100%;
  height: 250px;
  margin-bottom: 30px;
}
.workers-list__name {
  margin: 0;
  margin-bottom: 10px;
  font-size: 18px;
  text-transform: uppercase;
}
.workers-list__position {
  margin: 0;
  margin-bottom: 40px;
  font-size: 0.8em;
}
.workers__info {
  align-self: flex-start;
  min-width: 270px;
  max-width: 30%;
  padding: 40px;
  border: 2px solid #03587f;
}
.workers-info__quantity {
  position: relative;
  display: inline-block;
  margin-bottom: 10px;
  font-size: 30px;
}
.workers-info__quantity::after {
  content: "";
  position: absolute;
  left: calc(100% + 20px);
  bottom: 6px;
  width: calc(100% + 10px);
  height: 2px;
  background-color: #03587f;
}
.workers-info__title {
  margin: 0;
  margin-bottom: 30px;
}
.workers-info__text {
  margin: 0;
}
.workers-content {
  margin-bottom: 40px;
}
.workers-content hr {
  margin: 20px 0;
  height: 1px;
  border: none;
  background-image: linear-gradient(to left, #fff, #03587f, #fff);
}
.workers-content h3 {
  margin: 0;
}
.workers-content span {
  font-size: 0.8em;
  margin: 0;
}
.workers-content__list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none;
}
.workers-content__item {
  display: flex;
  flex-direction: column;
  width: 200px;
  margin-bottom: 40px;
  margin-right: 30px;
}
.workers-content__item:last-child {
  margin-right: 0;
}
.workers-content__item--active {
  flex-direction: row;
  justify-content: space-between;
  order: -1;
  width: 100%;
  padding-bottom: 40px;
  border-bottom: 1px solid #f2f2f2;
}
.workers-content__img {
  margin-bottom: 20px;
}
.workers-content__img img {
  display: block;
  width: 100%;
  height: 230px;
}
.workers-content__item--active .workers-content__info {
  flex-grow: 1;
}
.workers-content__item--active .workers-content__img {
  width: 30%;
  margin-bottom: 0;
  margin-right: 40px;
}
.workers-content__item--active .workers-content__img img {
  width: auto;
  height: auto;
}
.partners {
  position: relative;
  margin-bottom: 10px;
  padding-bottom: 40px;
}
.partners::after {
  content: "";
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 2px;
  background-image: linear-gradient(to left, #ffffff, #03587f, #ffffff);
}
.partners__wrapper {
  max-width: 1150px;
  margin: 0 auto;
  padding: 0 50px;
}
.partners__title {
  margin-bottom: 40px;
}
.partners__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.partners-list {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.partners-list__item {
  min-width: 230px;
  width: 25%;
  margin: auto;
}
.partners-list__img {
  display: flex;
  height: 130px;
  filter: grayscale(1);
  transition: filter 0.2s ease-in-out, opacity 0.2s ease-in-out;
}
.partners-list__img:hover {
  filter: grayscale(0);
}
.partners-list__img:active {
  opacity: 0.6;
}
.partners-list__img img {
  display: block;
  margin: auto;
}
.breadcrumbs {
  margin: 40px 0;
}
.breadcrumbs__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.breadcrumbs-list {
  display: flex;
}
.breadcrumbs-list__item {
  position: relative;
  margin-right: 30px;
}
.breadcrumbs-list__item::after {
  content: "/";
  position: absolute;
  top: 50%;
  right: -18px;
  color: #8c8c8c;
  transform: translateY(-50%);
}
.breadcrumbs-list__item--active {
  margin-right: 0;
}
.breadcrumbs-list__item--active::after {
  content: none;
}
.breadcrumbs-list__item a {
  color: #8c8c8c;
}
.breadcrumbs-list__item--active a {
  color: #cecece;
}
.page-footer {
  color: #fff;
  background-color: #0b2c45;
}
.page-footer .site-list__link {
  color: #fff;
}
.page-footer .site-list__link--active {
  border-color: #fff;
}
.page-footer__top {
  margin-bottom: 40px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.3);
}
.page-footer__nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 100px;
  padding: 0 50px;
}
.page-footer__logo {
  transition: 0.2s ease-in-out;
}
.page-footer__logo:hover,
.page-footer__logo:focus {
  opacity: 0.7;
}
.page-footer__logo:active {
  opacity: 0.3;
}
.page-footer__middle {
  padding-bottom: 40px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.3);
}
.page-footer__bottom {
  padding-top: 5px;
  padding-bottom: 5px;
  text-align: center;
}
.page-footer__copyright {
  margin: 0;
}
.page-footer__developed {
  width: 290px;
  margin: 0;
}
.page-inner .page-footer {
  margin-top: 80px;
}
.contacts {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.contacts__title {
  margin-bottom: 50px;
  color: #ffffff;
}
.contacts__title::after {
  background-color: #ffffff;
}
.contacts__info {
  min-width: 300px;
  width: 23vw;
  font-size: 14px;
}
.contacts__info a {
  color: #ffffff;
}
.contacts__info-address {
  margin-bottom: 20px;
  font-style: normal;
}
.contacts__info-address span {
  font-weight: 700;
  font-size: 18px;
  text-transform: uppercase;
}
.contacts__info-tel {
  display: block;
  margin-bottom: 20px;
  font-weight: 700;
  font-size: 18px;
  text-transform: uppercase;
}
.contacts__info-tel span {
  font-weight: 400;
  font-size: 16px;
  transition: 0.2s ease-in-out;
}
.contacts__info-tel span:hover,
.contacts__info-tel span:focus {
  color: #00aeff;
}
.contacts__info-tel span:active {
  opacity: 0.6;
}
.contacts__info-email {
  display: inline-block;
  margin-bottom: 20px;
  transition: 0.2s ease-in-out;
}
.contacts__info-email:hover,
.contacts__info-email:focus {
  color: #00aeff;
}
.contacts__info-email:active {
  opacity: 0.6;
}
.contacts__info-timerable {
  margin: 0;
  margin-bottom: 20px;
}
.contacts__form {
  display: flex;
  flex-direction: column;
  min-width: 300px;
  width: 40vw;
  max-width: 600px;
}
.contacts__form-item {
  margin: 0;
  margin-bottom: 30px;
}
.contacts__form-input,
.contacts__form-textarea {
  width: 100%;
  padding: 20px;
  box-sizing: border-box;
  color: #ffffff;
  border: 1px solid #ffffff;
  background-color: transparent;
  transition: border-color 0.2s ease-in-out;
}
.contacts__form-input:hover,
.contacts__form-textarea:hover,
.contacts__form-input:focus,
.contacts__form-textarea:focus {
  border-color: #00aeff;
  outline: none;
}
.contacts__form-input::placeholder,
.contacts__form-textarea::placeholder {
  color: #ffffff;
  transition: 0.2s ease-in-out;
}
.contacts__form-input:hover::placeholder,
.contacts__form-textarea:hover::placeholder {
  opacity: 0.7;
}
.contacts__form-textarea {
  height: 160px;
  resize: vertical;
}
.social__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.social-list {
  display: flex;
  flex-wrap: wrap;
  width: 250px;
}
.social-list__item {
  display: flex;
  margin-right: 10px;
}
.social-list__item:last-child {
  margin-right: 0;
}
.social__link {
  display: flex;
  margin: auto;
  padding: 10px;
  opacity: 1;
  transition: opacity 0.2s ease-in-out;
}
.social__link svg {
  fill: #ffffff;
  transition: 0.2s ease-in-out;
}
.social__link:hover svg,
.social__link:focus svg {
  fill: #00aeff;
}
.social__link:active {
  opacity: 0.6;
}
