.breadcrumbs {
	margin: 40px 0;
}

.breadcrumbs__list {
	margin: 0;
	padding: 0;
	list-style: none;
}

.breadcrumbs-list {
	display: flex;
}

.breadcrumbs-list__item {
	position: relative;
	margin-right: 30px;
}

.breadcrumbs-list__item::after {
	content: "/";
	position: absolute;
	top: 50%;
	right: -18px;
	color: #8c8c8c;
	transform: translateY(-50%);
}

.breadcrumbs-list__item--active {
	margin-right: 0;
}

.breadcrumbs-list__item--active::after {
	content: none;
}

.breadcrumbs-list__item a {
	color: #8c8c8c;
}

.breadcrumbs-list__item--active a {
	color: #cecece;
}
