.goods {
  position: relative;
  margin-bottom: 60px;
  padding-bottom: 40px;

  &::after {
    .after-decor();
  }
}

.goods__wrapper {
  display: flex;
  flex-wrap: wrap;
  max-width: 1150px;
  margin: 0 auto;
  padding: 0 50px;
}

.goods__title {
	margin-bottom: 50px;
}

.goods__list {
	margin: 0;
	margin-left: 50px;
	padding: 0;
	list-style: none;
}

.goods-list {
	display: flex;
	justify-content: space-between;
  flex-wrap: wrap;
  width: 100%;
}

.goods-list__item {
	flex-grow: 1;
	width: 31%;
	max-width: 50%;
	margin-right: 30px;
	margin-bottom: 30px;
	border-radius: 15px;
	border-bottom: 5px solid #03587f;
	box-shadow: 0 4px 5px 2px #efefef;
	transition: border-color 0.2s ease-in-out,
  box-shadow 0.2s ease-in-out;

  &:nth-child(3n),
  &:last-child {
    margin-right: 0;
  }

  &:hover {
    border-color: @color_text_hover;
    box-shadow: 0 4px 5px 2px #dadada;
  }
}

.goods-list__link {
	display: flex;
  flex-direction: column;
	color: #03587f;
}

.goods-list__img {
	display: block;
  width: 100%;
  height: 250px;
	margin: auto;
	padding: 10px;
	border-bottom: 1px solid #efefef;
}

.goods-list__title {
	margin: 0;
	padding: 20px 0;
	text-align: center;
}
