.page-footer {
	color: #fff;
  background-color: #0b2c45;

  .site-list__link {
    color: #fff;

    &--active {
      border-color: #fff;
    }
  }
}

.page-footer__top {
  margin-bottom: 40px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.3);
}

.page-footer__nav {
	display: flex;
	justify-content: space-between;
	align-items: center;
  min-height: 100px;
	padding: 0 50px;
}

.page-footer__logo {
  transition: 0.2s ease-in-out;

  &:hover,
  &:focus {
    opacity: 0.7;
  }

  &:active {
    opacity: 0.3;
  }
}

.page-footer__middle {
  padding-bottom: 40px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.3);
}

.page-footer__bottom {
  padding-top: 5px;
  padding-bottom: 5px;
  text-align: center;
}

.page-footer__copyright {
  margin: 0;
}

.page-footer__developed {
	width: 290px;
  margin: 0;
}

.page-inner .page-footer {
	margin-top: 80px;
}
