.map {
  position: relative;
  min-height: 350px;

  &::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 80px;
    z-index: 100;
    position: absolute;
    background-size: 200% 160px;
    background-image: url(../img/decor/decor-wave.svg);
  }
}

.map__img {
  position: absolute;
  z-index: -1;
  width: 100%;
}

.map__img img {
  display: block;
  height: 100%;
  margin: auto;
}
