.font() {
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 16px;
  line-height: 24px;
  font-weight: normal;
}

.list-reset() {
  margin: 0;
  padding: 0;
  list-style: none;
}

.after-decor() {
  content: "";
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 2px;
  background-image: linear-gradient(to left, #ffffff, #03587f, #ffffff);
}
