.catalog-content {
  display: flex;
  margin-bottom: 80px;

  hr {
    margin: 30px 0;
    height: 1px;
    border: none;
    background-image: linear-gradient(to left, #fff, #03587f, #fff);
  }

  h3 {
    margin: 0;
    margin-bottom: 20px;
  }

  p {
    margin: 0;
    margin-bottom: 10px;
  }
}

.catalog-content__left-col {
  width: 40%;
  margin-right: 40px;

  img {
    display: block;
    width: 100%;
    height: 300px;
    margin-bottom: 30px;

    &:last-child {
      margin: 0;
    }
  }
}

.catalog-content__right-col {
  width: 50%;
}
