.workers-content {
  margin-bottom: 40px;

  hr {
    margin: 20px 0;
    height: 1px;
    border: none;
    background-image: linear-gradient(to left, #fff, #03587f, #fff);
  }

  h3 {
    margin: 0;
  }

  span {
    font-size: 0.8em;
    margin: 0;
  }
}

.workers-content__list {
  display: flex;
  flex-wrap: wrap;
  .list-reset();
}

.workers-content__item {
  display: flex;
  flex-direction: column;
  width: 200px;
  margin-bottom: 40px;
  margin-right: 30px;

  &:last-child {
    margin-right: 0;
  }

  &--active {
    flex-direction: row;
    justify-content: space-between;
    order: -1;
    width: 100%;
    padding-bottom: 40px;
    border-bottom: 1px solid #f2f2f2;
  }
}

.workers-content__img {
  margin-bottom: 20px;

  img {
    display: block;
    width: 100%;
    height: 230px;
  }
}

.workers-content__item--active .workers-content__info {
  flex-grow: 1;
}

.workers-content__item--active .workers-content__img {
  width: 30%;
  margin-bottom: 0;
  margin-right: 40px;

  img {
    width: auto;
    height: auto;
  }
}
