.news-content {
  margin-bottom: 40px;
}

.news-content__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  .list-reset();
}

.news-content__item {
  width: 32%;
  margin-bottom: 50px;
  border: 1px solid #f2f2f2;
  transition: box-shadow 0.2s ease-in-out;

  &:hover {
    box-shadow: 0 10px 14px -5px rgba(0, 0, 0, 0.6);
  }

  img {
    display: block;
    width: 100%;
    height: 250px;
    padding-top: 10px;
  }
}

.news-content__item-desc {
  display: flex;
  flex-wrap: wrap;
  padding: 10px;
}

.news-content__item-title {
  margin: 0;
  margin-bottom: 10px;
  font-weight: @font_weight_bold;
  color: #03587f;
  text-align: center;
}

.news-content__item-text {
  height: 100px;
  margin: 0;
  color: #002744;
  overflow: hidden;
}

.news-content__item-date {
  color: #777777;
}

.news-content__item-more {
  margin-left: auto;
  color: #03587f;
  transition: color 0.2s ease-in-out,
    opacity 0.2s ease-in-out;

  &:hover,
  &:focus {
    color: @color_text_hover;
  }

  &:active {
    opacity: 0.6;
  }
}
