.btn {
  display: inline-block;
  padding: 10px 50px;
  font-size: 15px;
  line-height: 24px;
  color: #03587f;
  text-transform: uppercase;
  vertical-align: middle;
  border-radius: 29px;
  border: 2px solid #03587f;
  background-color: transparent;
  transition: color 0.2s ease-in-out,
    border-color 0.2s ease-in-out,
    box-shadow 0.2s ease-in-out;
  cursor: pointer;

  &:hover,
  &:focus {
    color: @color_text_hover;
    border-color: @color_text_hover;
    outline: none;
  }

  &:active {
    box-shadow: 0 2px 5px 0 rgba(0, 174, 255, 0.6);
  }
}

.btn__about {
  margin: 0 auto;
  margin-bottom: 30px;
  color: @color_text_hover;
  border-color: @color_text_hover;
}

.btn__goods {
  align-self: flex-start;
  margin-left: auto;
}

.btn__news {
  align-self: flex-start;
  margin-left: auto;
}

.btn__workers {
  align-self: flex-start;
  margin-left: auto;
}

.btn__form {
  align-self: center;
  color: #ffffff;
  border-color: #ffffff;
}
