.page-title {
  margin: 0;
  margin-bottom: 30px;
  padding: 76px 0;
  font-size: 27px;
  line-height: 30px;
  font-weight: @font_weight_bold;
  color: #000000;
  text-align: center;
}

.goods__title,
.news__title,
.workers__title,
.partners__title,
.contacts__title {
  position: relative;
  display: inline-block;
  margin: 0;
  font-weight: 700;
  font-size: 36px;
  line-height: 1;
  color: #03587f;
  text-transform: uppercase;

  &::after {
    content: "";
    position: absolute;
    left: calc(100% + 20px);
    bottom: 6px;
    width: 60px;
    height: 2px;
    background-color: #03587f;
  }
}
