.news {
  position: relative;
  margin-bottom: 60px;
  padding-bottom: 40px;
}

.news__wrapper {
  display: flex;
  flex-wrap: wrap;
  max-width: 1150px;
  margin: 0 auto;
  padding: 0 50px;
}

.news__title {
  margin-bottom: 50px;
}

.news__list {
  margin: 0;
  margin-left: 50px;
  padding: 0;
  list-style: none;
}

.news-list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 100%;
}

.news-list__item {
  position: relative;
  min-width: 250px;
  width: 31%;
  margin-right: 30px;
  margin-bottom: 30px;
  border-radius: 15px;
  border-bottom: 5px solid #03587f;
  box-shadow: 0 4px 5px 2px #efefef;
  transition: border-color 0.2s ease-in-out,
    box-shadow 0.2s ease-in-out;

  &:nth-child(3n),
  &:last-child {
    margin-right: 0;
  }

  &:hover {
    border-color: @color_text_hover;
    box-shadow: 0 5px 5px 2px #dadada;

    .news-item__details-link {
      background-color: rgba(0, 135, 197, 0.8);
      opacity: 1;
      transition: 0.2s ease-in-out;
    }
  }
}

.news-item__img-wrapper {
  position: relative;
}

.news-item__img {
  display: block;
  width: 100%;
  height: 156px;
  margin: auto;
  padding: 10px;
  padding-bottom: 0;
}

.news-item__details-link {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  text-align: center;
  border-radius: 15px 15px 0 0;
  background-color: #0087c5;
  opacity: 0;
}

.news-item__details-link {
  display: block;
  padding: 66px 0;
  color: #fff;

  &:hover {
    text-decoration: underline;
  }
}

.news-item__desc {
  padding: 20px;
}

.news-item__title {
  margin: 0;
  margin-bottom: 20px;
}

.news-item__text {
  height: 100px;
  margin: 0;
  color: #002744;
  overflow: hidden;
}

.news-item__date {
  color: #777777;
}
